import ROSLIB from "roslib";

export const AgvActionType = {
  Nothing: -1,
  Manual: 0,
  Movebase: 1,
  InitializePose: 2,
  PlugCharger: 3,
  UnplugCharger: 4,
  LiftUp: 5,
  LiftDown: 6,
}

export default class AgvrAction {
  #agvActionTopic = null;

  constructor(ros) {
    if (!(ros instanceof ROSLIB.Ros)) return;

    this.#agvActionTopic = new ROSLIB.Topic({
      ros: ros,
      name: '/agv_action',
      messageType: 'move_base/agv_action',
    });
  }

  pushAction(action) {
    if (this.#agvActionTopic == null || !Number.isInteger(action)) return;
    this.#agvActionTopic.publish(new ROSLIB.Message({
      header: { seq: 0, stamp: 0, frame_id: '', },
      action: action,
      status: 0,
      action_id: "test",
    }))
  }

  dispose() {
    this.#agvActionTopic = null;
  }
}