<template>
  <line
    :x1="startWayPoint.x"
    :y1="startWayPoint.y"
    :x2="endWayPoint.x"
    :y2="endWayPoint.y"
    stroke-dasharray="10,3"
    style="stroke: rgb(0, 0, 255); stroke-width: 2"
  />
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "AgvrWayOnMap",
  props: {
    taskIndex: {
      type: Number,
      default: -1,
    },
  },
  computed: {
    ...mapState({
      startWayPoint(state) {
        return state.ros.jobs[state.navigation.selectedJobId].tasks[this.taskIndex - 1].position;
      },
      endWayPoint(state) {
        return state.ros.jobs[state.navigation.selectedJobId].tasks[this.taskIndex].position;
      },
    })
  }
};
</script>