<template>
  <v-navigation-drawer
    v-model="isShow"
    absolute
    temporary
    right
    width="500"
    :style="{ boxShadow: 'none' }"
  >
    <v-container fluid class="pa-0">
      <v-row class="flex-shrink-1">
        <v-col>
          <v-stepper class="action-management-stepper" v-model="step" vertical>
            <template v-for="(stepItem, index) in actionCollection">
              <div
                :key="`step-${stepItem.step}`"
                @contextmenu="(e) => openMenuContext(e, index)"
              >
                <v-stepper-step
                  :step="stepItem.step"
                  :edit-icon="'mdi-puzzle-edit-outline'"
                  complete
                  editable
                >
                  {{ stepItem.name }}
                </v-stepper-step>
              </div>

              <v-stepper-content
                :key="`stepper-content-${stepItem.step}`"
                :step="stepItem.step"
              >
                <v-container fluid class="pa-0">
                  <v-text-field
                    class="mt-2"
                    label="Step name"
                    v-model="stepItem.name"
                    append-icon="mdi-square-edit-outline"
                    outlined
                    dense
                  />
                  <v-combobox
                    label="Action"
                    v-model="stepItem.action"
                    :items="actionLists"
                    dense
                    hide-details
                    outlined
                  >
                    <template v-slot:selection="{ item }">
                      <span class="pr-2">
                        {{ actionTextLists[item] }}
                      </span>
                    </template>
                    <template v-slot:item="{ item }">
                      <span class="pr-2">
                        {{ actionTextLists[item] }}
                      </span>
                    </template>
                  </v-combobox>
                </v-container>
              </v-stepper-content>
            </template>
            <div @contextmenu="(e) => e.preventDefault()">
              <v-stepper-step
                :step="-1"
                edit-icon="mdi-plus"
                complete
                editable
                @click="addLastStep"
              />
            </div>
          </v-stepper>
        </v-col>
      </v-row>
      <v-row>
        <v-spacer />
        <v-col cols="auto" class="px-6">
          <v-btn depressed color="primary" v-text="'Save'" @click="onSave" />
        </v-col>
      </v-row>
    </v-container>

    <v-menu
      v-model="isShowMenu"
      :position-x="menuX"
      :position-y="menuY"
      absolute
      offset-y
    >
      <v-list>
        <v-list-item @click="addAboveStep">
          <v-list-item-icon>
            <v-icon>mdi-plus</v-icon>
          </v-list-item-icon>
          <v-list-item-title v-text="'Above'" />
        </v-list-item>
        <v-list-item @click="addBelowStep">
          <v-list-item-icon>
            <v-icon>mdi-plus</v-icon>
          </v-list-item-icon>
          <v-list-item-title v-text="'Below'" />
        </v-list-item>
        <v-list-item @click="deleteStep">
          <v-list-item-icon>
            <v-icon>mdi-delete</v-icon>
          </v-list-item-icon>
          <v-list-item-title v-text="'Delete'" />
        </v-list-item>
      </v-list>
    </v-menu>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters, mapState, mapActions } from "vuex";
import { uuid } from "vue-uuid";
import { AgvActionType } from "@/services/agvr/agvr-action";

export default {
  name: "AgvrActionManagement",
  data() {
    return {
      step: -1,
      actionLists: [
        AgvActionType.Nothing,
        AgvActionType.PlugCharger,
        AgvActionType.UnplugCharger,
        AgvActionType.LiftUp,
        AgvActionType.LiftDown,
      ],
      actionTextLists: {
        [AgvActionType.Nothing]: "Do nothing",
        [AgvActionType.PlugCharger]: "Plug charge",
        [AgvActionType.UnplugCharger]: "Unplug charge",
        [AgvActionType.LiftUp]: "Lift up",
        [AgvActionType.LiftDown]: "Lift down",
      },
      actionCollection: [],
      isShowMenu: false,
      menuX: 0,
      menuY: 0,
      selectedStep: -1,
    };
  },
  methods: {
    ...mapActions({
      navigationShowActionManagement: "navigation/showActionManagement",
      navigationHideActionManagement: "navigation/hideActionManagement",
      updateActionsOfSelectedTask: "updateActionsOfSelectedTask",
    }),
    openMenuContext(e, step) {
      this.selectedStep = step;
      e.preventDefault();
      this.isShowMenu = false;
      this.menuX = e.clientX;
      this.menuY = e.clientY;
      this.$nextTick(() => {
        this.isShowMenu = true;
      });
    },
    addAboveStep() {
      if (this.selectedStep < 0) return;
      this.actionCollection.splice(this.selectedStep, 0, {
        step: uuid.v4(),
        name: "New step",
        action: this.actionLists[0],
      });
    },
    addBelowStep() {
      if (this.selectedStep < 0) return;
      this.actionCollection.splice(this.selectedStep + 1, 0, {
        step: uuid.v4(),
        name: "New step",
        action: this.actionLists[0],
      });
    },
    deleteStep() {
      if (this.selectedStep < 0) return;
      this.actionCollection.splice(this.selectedStep, 1);
      this.step = -1;
    },
    addLastStep() {
      this.actionCollection.push({
        step: uuid.v4(),
        name: "New step",
        action: this.actionLists[0],
      });
    },
    onSave() {
      console.log(this.actionCollection);
      this.updateActionsOfSelectedTask({
        actions: this.actionCollection,
      });
    },
  },
  computed: {
    ...mapState({
      navigationActionManagementShow: (state) =>
        state.navigation.actionManagement.isShow,
    }),
    ...mapGetters({
      getListActionOfSelectedTask: "getListActionOfSelectedTask",
    }),
    isShow: {
      get() {
        return this.navigationActionManagementShow;
      },
      set(val) {
        if (val) {
          this.navigationShowActionManagement();
        } else {
          this.navigationHideActionManagement();
        }
      },
    },
  },
  watch: {
    isShowMenu(val) {
      if (val == false) this.selectedStep = -1;
    },
    isShow(val) {
      if(val) {
        this.actionCollection = this.getListActionOfSelectedTask();
      }
      else{
        this.actionCollection = [];
        this.step = -1;
      }
    }
  },
};
</script>

<style scoped>
.action-management-stepper {
  box-shadow: none;
}
</style>
