<template>
  <line
    :x1="positionX1"
    :y1="positionY1"
    :x2="positionX2"
    :y2="positionY2"
    stroke-dasharray="10,3"
    style="stroke: rgb(0, 0, 255); stroke-width: 2"
  />
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "AgvrWayStartOnMap",
  computed: {
    ...mapState({
      endWayPoint: (state) => {
        if(state.ros.jobs[state.navigation.selectedJobId].tasks.length > 0) {
          return state.ros.jobs[state.navigation.selectedJobId].tasks[0].position;
        }
        else{
          return null;
        }
      },
      startWayPoint(state) {
        return state.ros.agvs[state.navigation.selectedAgvId].webPose.position;
      },
    }),
    positionX1(){
      return this.startWayPoint.x.toFixed(0);
    },
    positionY1() {
      return this.startWayPoint.y.toFixed(0);
    },
    positionX2() {
      return ((this.endWayPoint != null) ? this.endWayPoint.x : this.startWayPoint.x).toFixed(0);
    },
    positionY2() {
      return ((this.endWayPoint != null) ? this.endWayPoint.y : this.startWayPoint.y).toFixed(0);
    },

  }
};
</script>