<template>
  <v-container class="pa-0" fluid>
    <v-row no-gutters justify="center">
      <v-col class="pa-1 pt-2" xs="12" sm="4" md="3" lg="2">
        <v-text-field
          type="number"
          color="black"
          :prepend-inner-icon="'mdi-axis-x-arrow'"
          append-icon="mdi-alpha-m"
          label="X axit"
          outlined
          dense
          :step="0.001"
          v-model="positionX"
          :readonly="readOnly"
          @keypress.enter="updatePosition"
          @blur="updatePosition"
          hide-details
        />
      </v-col>
      <v-col class="pa-1 pt-2" xs="12" sm="4" md="3" lg="2">
        <v-text-field
          type="number"
          color="black"
          :prepend-inner-icon="'mdi-axis-y-arrow'"
          append-icon="mdi-alpha-m"
          label="Y axit"
          outlined
          dense
          :step="0.001"
          v-model="positionY"
          :readonly="readOnly"
          @keypress.enter="updatePosition"
          @blur="updatePosition"
          hide-details
        />
      </v-col>
      <v-col class="pa-1 pt-2" xs="12" sm="4" md="3" lg="2">
        <v-text-field
          type="number"
          color="black"
          :prepend-inner-icon="'mdi-axis-z-rotate-counterclockwise'"
          append-icon="mdi-alpha-d"
          label="Z angle"
          outlined
          dense
          :step="0.001"
          v-model="angleZ"
          :readonly="readOnly"
          @keypress.enter="updateAngle"
          @blur="updateAngle"
          hide-details
        />
      </v-col>
      <v-col>
        Quality
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "AgvrPoseEditor",
  data() {
    return {
      tmpPositionX: 0,
      tmpPositionY: 0,
      tmpAngleZ: 0,
    };
  },
  props: {
    readOnly: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    ...mapActions({
      setSelectedAgvPosition: "setSelectedAgvPosition",
      setSelectedAgvDirection: "setSelectedAgvDirection",
    }),
    updatePosition() {
      this.setSelectedAgvPosition({
        x:
          ((this.tmpPositionX - this.agvRosMapInfo.origin.position.x) *
            this.agvMapScale) /
          this.agvRosMapInfo.resolution,
        y:
          ((this.agvRosMapInfo.origin.position.y - this.tmpPositionY) /
            this.agvRosMapInfo.resolution +
            this.agvRosMapInfo.height) *
          this.agvMapScale,
      });
    },
    updateAngle() {
      this.setSelectedAgvDirection({
        theta: this.tmpAngleZ,
      });
    },
  },
  computed: {
    ...mapState({
      agvMapScale: (state) =>
        state.ros.maps[state.navigation.selectedMapId].webMap.scale,
      agvRosMapInfo: (state) =>
        state.ros.maps[state.navigation.selectedMapId].rosMap.info,
      agvWebPose: (state) =>
        state.ros.agvs[state.navigation.selectedAgvId].webPose,
    }),
    positionX: {
      get() {
        return (
          this.agvRosMapInfo.origin.position.x +
          (this.agvWebPose.position.x * this.agvRosMapInfo.resolution) /
            this.agvMapScale
        ).toFixed(2);
      },
      set(val) {
        this.tmpPositionX = val;
      },
    },
    positionY: {
      get() {
        return (
          this.agvRosMapInfo.origin.position.y -
          (this.agvWebPose.position.y / this.agvMapScale -
            this.agvRosMapInfo.height) *
            this.agvRosMapInfo.resolution
        ).toFixed(2);
      },
      set(val) {
        this.tmpPositionY = val;
      },
    },
    angleZ: {
      get: function () {
        return this.agvWebPose.orientation.theta.toFixed(0);
      },
      set: function (val) {
        this.tmpAngleZ = val;
      },
    },
  },
  mounted() {
    this.tmpPositionX = this.positionX;
    this.tmpPositionY = this.positionY;
    this.tmpAngleZ = this.angleZ;
  },
};
</script>

<style scoped>
.pose-input {
  caret-color: black;
}
</style>