<template>
  <foreignObject
    class="panzoom-map-image"
    :width="width"
    :height="height"
  >
    <canvas ref="mapImageRef" />
  </foreignObject>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "AgvrRosMapImage",
  data: () => ({
    mapCanvas: null,
    width: 0,
    height: 0,
  }),
  computed: {
    ...mapState({
      navigationSvgMapImage: (state) => state.agvMaps[state.navigation.selectedMapId].webMap.Image,
    }),
  },
  mounted() {
    this.mapCanvas = this.$refs.mapImageRef;
      this.setMapData();
  },
  methods: {
    setMapData() {
      if(this.navigationSvgMapImage == null) return;
      this.mapCanvas.width = this.navigationSvgMapImage.width;
      this.mapCanvas.height = this.navigationSvgMapImage.height;
      let ctx = this.mapCanvas.getContext("2d");
      let imageData = ctx.createImageData(
        this.navigationSvgMapImage.width,
        this.navigationSvgMapImage.height
      );
      imageData.data.set(this.navigationSvgMapImage.data);
      ctx.putImageData(imageData, 0, 0);
      console.log("putImageData ", imageData);
      this.width = this.navigationSvgMapImage.width;
      this.height = this.navigationSvgMapImage.height;
    },
  },
  watch: {
    navigationSvgMapImage() {
      this.setMapData();
    },
  },
};
</script>
<style scoped>
.panzoom-map-image {
  z-index: -1;
  position: relative;
}
</style>