<template>
  <circle
    :cx="agvTask.position.x"
    :cy="agvTask.position.y"
    r="8"
    :stroke="selectedIndexTask == taskIndex ? 'blue' : 'red'"
    stroke-width="3"
    fill="red"
    @mousedown="onMouseDown"
    @touchstart="onMouseDown"
    @contextmenu="onContextMenu"
  />
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "AgvrTaskOnMap",
  props: {
    taskIndex: {
      type: Number,
      default: -1,
    },
  },
  computed: {
    ...mapState({
      agvTask(state) {
        return state.ros.jobs[state.navigation.selectedJobId].tasks[
          this.taskIndex
        ];
      },
      selectedIndexTask: (state) => state.navigation.selectedIndexTask,
      isContextMenuShow: (state) =>
        state.navigation.taskMenuContext.isShow ||
        state.navigation.agvMenuContext.isShow ||
        state.navigation.agvInitPoseMenuContext.isShow,
    }),
  },
  methods: {
    ...mapActions({
      navigationSetSelectedIndexTask: "navigation/setSelectedIndexTask",
      navigationShowTaskMenuContext: "navigation/showTaskMenuContext",
      navigationDisableMapPanZoom: "navigation/disableMapPanZoom",
      navigationSelectedTaskPosition: "navigation/selectedTaskPosition",
    }),
    onMouseDown() {
      if (this.isContextMenuShow) return;
      this.navigationSelectedTaskPosition();
      this.navigationDisableMapPanZoom();
      this.navigationSetSelectedIndexTask(this.taskIndex);
    },
    onContextMenu(e) {
      e.preventDefault();
      e.stopPropagation();
      this.$nextTick(() => {
        this.navigationShowTaskMenuContext({
          x: e.clientX,
          y: e.clientY,
        });
      });
    },
    update() {
      console.log("update");
    },
  },
};
</script>