<template>
  <g :visibility="agvTask != null ? 'visible' : 'hidden'">
    <defs>
      <marker
        id="arrowhead"
        markerWidth="3"
        markerHeight="2"
        refX="1"
        refY="1"
        orient="auto"
      >
        <polygon points="0 0, 3 1, 0 2" style="fill: limegreen" />
      </marker>
    </defs>
    <line
      :x1="agvTask != null ? agvTask.position.x : 0"
      :y1="agvTask != null ? agvTask.position.y : 0"
      :x2="
        agvTask != null
          ? agvTask.position.x +
            50 * Math.cos((agvTask.thetaEnd * Math.PI) / 180)
          : 0
      "
      :y2="
        agvTask != null
          ? agvTask.position.y -
            50 * Math.sin((agvTask.thetaEnd * Math.PI) / 180)
          : 0
      "
      stroke="limegreen"
      stroke-width="10"
      marker-end="url(#arrowhead)"
      @mousedown="onMouseDown"
      @touchstart="onMouseDown"
    />
  </g>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "AgvrEndTaskDirection",
  computed: {
    ...mapState({
      agvTask: (state) => {
        let job = state.ros.jobs[state.navigation.selectedJobId];
        if (job == null || job.tasks.length == 0) return null;
        return job.tasks[job.tasks.length - 1];
      },
      isContextMenuShow: (state) =>
        state.navigation.taskMenuContext.isShow ||
        state.navigation.agvMenuContext.isShow ||
        state.navigation.agvInitPoseMenuContext.isShow,
    }),
  },
  methods: {
    ...mapActions({
      navigationDisableMapPanZoom: "navigation/disableMapPanZoom",
      navigationSelectedTaskThetaEnd: "navigation/selectedTaskThetaEnd",
    }),
    onMouseDown() {
      if(this.isContextMenuShow) return;
      this.navigationDisableMapPanZoom();
      this.navigationSelectedTaskThetaEnd();
    },
  },
};
</script>