<template>
  <image
    href="@/assets/images/car.png"
    :transform="transformData"
    :x="positionX"
    :y="positionY"
    :height="(agvWebSize.height * agvMapScale) / agvMapResolution"
    :width="(agvWebSize.width * agvMapScale) / agvMapResolution"
    @contextmenu="onContextMenu"
    @mousedown="selectedRobot"
    @touchstart="selectedRobot"
  />
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "AgvrRobotx",
  data() {
    return {
      pathData: "",
      timeoutContextMenu: null,
    };
  },
  props: {
    selectable: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState({
      agvMapResolution: (state) =>
        state.ros.maps[state.navigation.selectedMapId].rosMap.info.resolution,
      agvWebSize: (state) =>
        state.ros.agvs[state.navigation.selectedAgvId].webSize,
      agvWebPose: (state) =>
        state.ros.agvs[state.navigation.selectedAgvId].webPose,
      agvMapScale: (state) =>
        state.ros.maps[state.navigation.selectedMapId].webMap.scale,

      isContextMenuShow: (state) =>
        state.navigation.taskMenuContext.isShow ||
        state.navigation.agvMenuContext.isShow ||
        state.navigation.agvInitPoseMenuContext.isShow,
    }),
    positionX() {
      return (
        this.agvWebPose.position.x -
        (this.agvWebSize.width * this.agvMapScale) / (this.agvMapResolution * 2)
      ).toFixed(0);
    },
    positionY() {
      return (
        this.agvWebPose.position.y -
        (this.agvWebSize.height * this.agvMapScale) /
          (this.agvMapResolution * 2)
      ).toFixed(0);
    },
    transformData() {
      return `rotate(${
        90 - this.agvWebPose.orientation.theta.toFixed(0)
      },${this.agvWebPose.position.x.toFixed(
        0
      )},${this.agvWebPose.position.y.toFixed(0)})`;
    },
  },
  methods: {
    ...mapActions({
      navigationShowAgvMenuContext: "navigation/showAgvMenuContext",
      navigationShowAgvInitPoseMenuContext:
        "navigation/showAgvInitPoseMenuContext",
      navigationDisableMapPanZoom: "navigation/disableMapPanZoom",
      navigationSelectedRobot: "navigation/selectedRobot",
    }),
    onContextMenu(e) {
      e.preventDefault();
      e.stopPropagation();
      this.$nextTick(() => {
        if (this.selectable) {
          this.navigationShowAgvInitPoseMenuContext({
            x: e.clientX,
            y: e.clientY,
          });
        } else {
          this.navigationShowAgvMenuContext({
            x: e.clientX,
            y: e.clientY,
          });
        }
      });
    },
    selectedRobot() {
      if (this.isContextMenuShow) return;
      this.navigationDisableMapPanZoom();
      if (this.selectable) {
        this.navigationSelectedRobot();
      }
    },
  },
};
</script>

