<template>
  <g>
    <defs>
      <marker
        id="arrowhead"
        markerWidth="3"
        markerHeight="2"
        refX="1"
        refY="1"
        orient="auto"
      >
        <polygon points="0 0, 3 1, 0 2" style="fill: limegreen" />
      </marker>
    </defs>
    <line
      :x1="agvPose.position.x"
      :y1="agvPose.position.y"
      :x2="
        agvPose.position.x +
        100 * Math.cos((agvPose.orientation.theta * Math.PI) / 180)
      "
      :y2="
        agvPose.position.y -
        100 * Math.sin((agvPose.orientation.theta * Math.PI) / 180)
      "
      stroke="limegreen"
      stroke-width="10"
      marker-end="url(#arrowhead)"
      @mousedown="selectedRobotDirection"
      @touchstart="selectedRobotDirection"
    />
  </g>
</template>
<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "AgvrRobotDirection",
  computed: {
    ...mapState({
      agvPose: (state) =>
        state.ros.agvs[state.navigation.selectedAgvId].webPose,
      isContextMenuShow: (state) =>
        state.navigation.taskMenuContext.isShow ||
        state.navigation.agvMenuContext.isShow ||
        state.navigation.agvInitPoseMenuContext.isShow,
    }),
  },
  methods: {
    ...mapActions({
      navigationDisableMapPanZoom: "navigation/disableMapPanZoom",
      navigationSelectedRobotDirection: "navigation/selectedRobotDirection",
    }),
    selectedRobotDirection() {
      if (this.isContextMenuShow) return;
      this.navigationDisableMapPanZoom();
      this.navigationSelectedRobotDirection();
    },
  },
};
</script>